@media (max-width: 1023px) {
  .menu {
    position: relative; } }

.menu__list {
  font-size: 16px;
  line-height: 200%;
  z-index: 2;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 875px;
  margin: 0 auto;
  font-weight: 300;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2); }
  @media (max-width: 1023px) {
    .menu__list {
      display: block;
      width: 100%;
      height: 100vh;
      margin: 0;
      padding-top: 100px;
      background-color: #ffffff;
      border: none; }
      .menu--opened .menu__list {
        position: absolute; }
      .menu--closed .menu__list {
        display: none; }
      .menu--nojs .menu__list {
        position: static; } }

@media (max-width: 1023px) {
  .menu__item {
    margin-bottom: 30px;
    background-color: #f6f6f6; } }

.menu__link {
  display: block;
  padding: 21px 0 9px 0;
  color: rgba(255, 255, 255, 0.7); }
  .menu__link:hover, .menu__link:focus {
    color: #0ad9c6; }
  @media (max-width: 1023px) {
    .menu__link {
      padding: 17px 16px;
      color: #48494d; } }

.menu__toggle {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 3;
  display: none;
  width: 50px;
  height: 50px;
  background-color: #0ad9c6; }
  .menu--nojs .menu__toggle {
    display: none; }
  @media (max-width: 1023px) {
    .menu__toggle {
      display: block; } }

.menu--closed .menu__toggle::before {
  content: "";
  position: absolute;
  top: 19px;
  left: 15px;
  width: 22px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 5px 0 0 #000000, 0 10px 0 0 #000000; }

.menu--opened .menu__toggle::before, .menu--opened .menu__toggle::after {
  content: "";
  position: absolute;
  top: 15px;
  left: 17px;
  width: 22px;
  height: 2px;
  background-color: #000000; }

.menu--opened .menu__toggle::before {
  transform: rotate(45deg); }

.menu--opened .menu__toggle::after {
  transform: rotate(-45deg); }

